:root {
  --main-color: #00acdc;
}

body {
  box-sizing: border-box;
  margin: 0;
}

.header__container {
  background-color: #fff;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 100%;
  max-width: 40px;
}

.nav {
  margin: 0;
  padding: 0;
}

.nav__list {
  box-sizing: border-box;
  text-transform: uppercase;
  flex-wrap: wrap;
  height: 60px;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  display: flex;
}

.nav__item {
  box-sizing: border-box;
  margin-left: 20px;
}

.nav__item:first-child {
  height: 60px;
  margin-left: 0;
  margin-right: 0;
  position: relative;
}

.nav__link {
  box-sizing: border-box;
  border-bottom: 4px solid #0000;
  transition: color .5s, border-color .5s;
}

.nav__link:hover {
  color: var(--main-color);
  border-bottom: 4px solid var(--main-color);
}

.is-active {
  color: var(--main-color);
  position: relative;
}

.is-active:after {
  content: "";
  border-bottom: 4px solid var(--main-color);
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:nth-child(4) {
  color: var(--main-color);
}
